.card {
    border: 1px solid #DDDDDD;
    border-radius: 10px;
    cursor: pointer;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;  
    width: 250px;
    height: 350px;
    text-align: left;
}
.card:hover {
    transform: scale(1.05);
}

.card h3{
    margin-bottom:0;
    margin-top: 0.5rem;
    font-size: 1.5rem;
    text-align: center;
}

.card p {
    margin-left: 2rem;
    margin-top: 0;
}

.card-id{
    font-size: 0.8rem;
}

.card ul {
    margin-top: 0;
}

.card-img {
    height: 200px;
    background-color: #F0F0F0;
    padding: 5%;
    text-align: center;
}

.card-img img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}
